.navigation__upper-part {
  display: flex;
  max-width: 1366px;
  justify-content: space-between;
  padding-left: 100px;
  padding-right: 100px;
  position: relative;
  margin-bottom: 37px; }

.navigation__inner {
  padding-top: 30px;
  padding-bottom: 19px;
  padding-left: 0;
  padding-right: 0; }

