  .navigation__link span {
    color: #ffffff; }
  .navigation__link--tablet span {
    color: #e9ded8; }
  .hidden {
    display: none; }
  .no-scroll {
    overflow: hidden; }
  .navigation__inner--open {
    background-color: #a87b62;
    padding-bottom: 80px; }
  .navigation__logo--open {
    color: #a87b62; }
