  .navigation__upper-part,
  .navigation__menu {
    padding-left: 15px;
    padding-right: 15px; }
  .navigation__upper-part::before {
    left: 34px; }
  .navigation__input-tablet-menu {
    min-width: 100%; } }

.header {
  margin-bottom: 130px;
  position: relative; }

.header::before {
  content: "";
  position: absolute;
  top: -70px;
  background-repeat: no-repeat;
  height: 1px;
  border: 1px solid #f6f5f4;
  width: 100%; }

.header__inner {
  padding-right: 0;
  padding-left: 0;
  position: relative;
  max-width: 1366px; }

.header__image {
  min-height: 463px; }

.header__information {
  position: absolute;
  top: 38%;
  left: 50%;
  transform: translateX(-50%);
  color: #ffffff; }

.header__title {
  font-family: "Cormorant-Garamond", "Arial", sans-serif;
  font-weight: 700;
  font-size: 45px;
  line-height: 58.5px;
  margin-top: 0;
  margin-bottom: 5px;
  letter-spacing: 3px;
  text-transform: uppercase; }

.header__text {
  font-family: "Cormorant-Garamond", "Arial", sans-serif;
  font-weight: 400;
  font-size: 38px;
  line-height: 49.4px;
  margin: 0;
  text-align: center;
  font-style: italic; }

@media (max-width: 1023px) {
  .header::before {
    display: none; }
  .header__image {
    object-fit: fill; } }

@media (max-width: 767px) {
  .header {
    margin-bottom: 70px; }
  .header__title {
    font-size: 38px;
    line-height: 49.4px; }
  .header__text {
    font-size: 34px;
    line-height: 44.2px; } }

.slider {
  margin-bottom: 130px; }

.slider__wrapper {
  margin-bottom: 60px;
  overflow: hidden; }

.slider__inner {
  max-width: 1200px; }

.slider__header {
  display: flex;
  justify-content: center;
  position: relative;
  margin-bottom: 60px; }

.slider__info {
  display: flex;
  flex-direction: column;
  text-align: center; }

.slider__title {
  margin-top: 0;
  margin-bottom: 35px;
  font-family: "Cormorant-Garamond", "Arial", sans-serif;
  font-weight: 700;
  font-size: 34px;
  line-height: 44.2px; }

.slider__link:hover span,
.slider__link:focus span,
.slider__link-title:hover span,
.slider__link-title:focus span {
  color: #1d1613; }

.slider__item:hover,
.slider__item:focus {
  opacity: 0.5; }

.slider__link-title,
.slider__link-title-card {
  font-weight: 500;
  position: relative; }

.slider__link-title::after,
.slider__link-title-card::after {
  content: "";
