  width: 80px; }

.slider__link-title::after {
  left: 10px;
  top: 20px; }

.slider__link-title-card::after {
  left: 85px;
  top: 20px; }

.slider__link-title:hover,
.slider__link-title:focus {
  opacity: 0.5; }

.slider__button {
  position: absolute;
  border: none;
  background-color: inherit;
  cursor: pointer;
  top: 35px;
  width: 29px;
  height: 10px; }

.slider__button--right,
.slider__button--left {
  background-repeat: no-repeat; }

.slider__button--right {
  right: 0;
  background-image: url("../img/arrow-right.svg"); }

.slider__button--left {
  left: 0;
  background-image: url("../img/arrow-left.svg"); }

.slider__button:hover,
.sloder__button:focus {
  opacity: 0.5; }

.slider__wrapper-inner {
