.slider__text {
  margin-top: 0;
  margin-bottom: 10px;
  font-weight: 500; }

.slider__price {
  margin: 0;
  font-size: 14px;
  line-height: 18.2px;
  font-weight: 700; }

.slider__list {
  list-style: none;
  padding-left: 0;
  display: flex;
  justify-content: center; }

.slider__buttons-mobile {
  display: none; }

.slider__link--tablet {
  display: none; }

.slider__item-button:not(:last-child) {
